.about {
    padding-top: 2em;
    align-items: center;
    display: flex;
    justify-content: center;    
    width: 100%;
}

.about p body div {
    font-size: 14px;
    letter-spacing: .8px;
    line-height: 25px;
    color: #000;
    padding: 10px;
    font-weight: 300;
}

.bio {
    display: flex;
    align-items: stretch;
    justify-content: center;
    flex-grow: 2;
    font-family: 'Roboto Mono', monospace;

}


.links {
    padding-left: 1.5em;
    display: flex;
    flex-direction: column;
}

.links .row {
    padding: 7px;
    display: flex;
}

.div {
    font-family: 'Roboto Mono', monospace;
      /* font-family: Helvetica; Roboto Mono, Helvetica,Arial,sans-serif; */
     font-size: 14px;
}



body, p {
    font-weight: 400;
    letter-spacing: .8px;
    line-height: 25px;
        color: black; /* You can also use 'white' or '#000' for black color */

}

.section {
    width: 60%;
}

.header {
    font-size: 2em;
    text-align: start;
    padding: 0.5em;
        width: 60%;

}

.landing--social {
  font-size: 35px;
  margin: 0 1rem;
    color: black; /* You can also use 'white' or '#000' for black color */

}